import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { addMatch } from '../store/scoutingSlice';

function Scout() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    teamNumber: '',
    matchNumber: '',
    autoPoints: 0,
    teleopPoints: 0,
    endgamePoints: 0,
    notes: '',
    alliance: '',
    position: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const selectTeam = (alliance, position) => {
    setFormData(prev => ({
      ...prev,
      alliance,
      position
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Add match data directly to Redux store
    dispatch(addMatch({
      ...formData,
      id: Date.now(), // Generate a unique ID
      timestamp: new Date().toISOString()
    }));
    
    // Reset form
    setFormData({
      teamNumber: '',
      matchNumber: '',
      autoPoints: 0,
      teleopPoints: 0,
      endgamePoints: 0,
      notes: '',
      alliance: '',
      position: ''
    });
    
    // Navigate to analysis page
    navigate('/analysis');
  };

  return (
    <div className="container">
      <h2 className="mb-4">Scout Match</h2>
      <form onSubmit={handleSubmit}>
        <div className="mb-3">
          <label className="form-label">Team Number</label>
          <input
            type="number"
            className="form-control"
            name="teamNumber"
            value={formData.teamNumber}
            onChange={handleChange}
            required
          />
        </div>
        
        <div className="mb-3">
          <label className="form-label">Match Number</label>
          <input
            type="number"
            className="form-control"
            name="matchNumber"
            value={formData.matchNumber}
            onChange={handleChange}
            required
          />
        </div>

        <div className="mb-3">
          <label className="form-label">Alliance Selection</label>
          <div className="d-flex gap-2 mb-2">
            <button
              type="button"
              className={`btn ${formData.alliance === 'red' ? 'btn-danger' : 'btn-outline-danger'}`}
              onClick={() => selectTeam('red', formData.position)}
            >
              Red Alliance
            </button>
            <button
              type="button"
              className={`btn ${formData.alliance === 'blue' ? 'btn-primary' : 'btn-outline-primary'}`}
              onClick={() => selectTeam('blue', formData.position)}
            >
              Blue Alliance
            </button>
          </div>
        </div>

        <div className="mb-3">
          <label className="form-label">Robot Position</label>
          <div className="d-flex gap-2">
            {[1, 2, 3].map(pos => (
              <button
                key={pos}
                type="button"
                className={`btn ${formData.position === pos ? 'btn-success' : 'btn-outline-success'}`}
                onClick={() => selectTeam(formData.alliance, pos)}
              >
                Position {pos}
              </button>
            ))}
          </div>
        </div>

        <div className="mb-3">
          <label className="form-label">Auto Points</label>
          <input
            type="number"
            className="form-control"
            name="autoPoints"
            value={formData.autoPoints}
            onChange={handleChange}
            required
          />
        </div>

        <div className="mb-3">
          <label className="form-label">Teleop Points</label>
          <input
            type="number"
            className="form-control"
            name="teleopPoints"
            value={formData.teleopPoints}
            onChange={handleChange}
            required
          />
        </div>

        <div className="mb-3">
          <label className="form-label">Endgame Points</label>
          <input
            type="number"
            className="form-control"
            name="endgamePoints"
            value={formData.endgamePoints}
            onChange={handleChange}
            required
          />
        </div>

        <div className="mb-3">
          <label className="form-label">Notes</label>
          <textarea
            className="form-control"
            name="notes"
            value={formData.notes}
            onChange={handleChange}
            rows="3"
          />
        </div>

        <button type="submit" className="btn btn-primary">Submit Match Data</button>
      </form>
    </div>
  );
}

export default Scout;
